<template>
    <div>
        <a-tabs :animated="false" default-active-key="1" @change="callback">
            <a-tab-pane key="1" tab="网页客服">
                <div v-if="!inserting" class="insert_none">
                    <p>您还未添加网页插件</p>
                    <a-button type="primary" size="large" @click="inserting=true">添加网页插件</a-button>
                </div>
                <div v-else>
                    <div class="btn-group-noe">
                        <a-button status="url" :class="{activeBtn:status==='url'}">网页设置</a-button>
                        <a-button status="css" :class="{activeBtn:status==='css'}">样式设置</a-button>
                        <a-button
                            :disabled="phoneDisabled"
                            status="phone"
                            :class="{activeBtn:status==='phone'}"
                        >电话设置</a-button>
                        <a-button
                            :disabled="wechatDisabled"
                            status="wechat"
                            :class="{activeBtn:status==='wechat'}"
                        >微信设置</a-button>
                        <a-button status="code" :class="{activeBtn:status==='code'}">安装代码</a-button>
                    </div>
                    <div v-if="status==='url'" class="insert_start">
                        <a-form
                            :form="form"
                            :label-col="{ span: 5 }"
                            :wrapper-col="{ span: 12 }"
                            @submit="handleSubmit"
                        >
                            <a-form-item label="网页网址">
                                <a-input
                                    size="large"
                                    v-decorator="['url', { validateFirst:true,rules: [{ required: true,message:'请输入网页网址'},{validator:validateToUrl}]}]"
                                />
                            </a-form-item>
                            <a-form-item
                                :wrapper-col="{ span: 12, offset: 5 }"
                                style="margin-top:60px;"
                            >
                                <a-button
                                    size="large"
                                    type="primary"
                                    html-type="submit"
                                    style="width:128px;"
                                >下一步</a-button>
                            </a-form-item>
                        </a-form>
                    </div>
                    <div v-else-if="status==='css'" class="insert_two">
                        <div style="display: flex;">
                            <div style="flex: 0 0 15%;">
                                <div class="border">
                                    <div
                                        class="checkbox-item"
                                        v-for="(tag,idx) in tags"
                                        :key="tag.title"
                                    >
                                        <a-checkbox
                                            @change="onChange"
                                            @click="currentIdx=idx"
                                            :checked="tag.checked"
                                        >{{tag.title}}</a-checkbox>
                                        <p>
                                            <a-icon
                                                type="arrow-up"
                                                v-if="idx!==0"
                                                @click="up(idx)"
                                            />
                                            <a-icon
                                                type="arrow-down"
                                                v-if="idx!==tags.length-1"
                                                @click="down(idx)"
                                            />
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div style="flex: 0 0 62%;">
                                <div class="border" style="padding:0;">
                                    <!-- <div class="radio-item-list">
                                    <a-radio-group v-model="mode" size="large">
                                        <a-radio-button value="pc" style="width:100px;">PC</a-radio-button>
                                        <a-radio-button value="mobile" style="width:100px;">手机</a-radio-button>
                                    </a-radio-group>
                                    </div>-->
                                    <div class="myiframe-box">
                                        <iframe class="myiframe" :src="url" frameborder="0"></iframe>
                                        <!-- <iframe class="" src="/static/qfy/index.html" frameborder="0"></iframe> -->
                                        <div class="qfy-btn-group" :style="styleObject">
                                            <div
                                                v-for="tag in tags"
                                                :key="tag.title"
                                                v-if="tag.checked"
                                            >{{tag.title}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div style="flex: 0 0 23%;">
                                <div class="border">
                                    <div style="margin: 20px 0;">
                                        <span>按钮位置：</span>
                                        <a-select
                                            size="large"
                                            placeholder="请选择按钮位置"
                                            v-model="position"
                                            style="width:160px;"
                                            @change="handleSelectChange"
                                        >
                                            <a-select-option
                                                v-for="item in positionList"
                                                :key="item.value"
                                                :value="item.value"
                                            >{{item.title}}</a-select-option>
                                        </a-select>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style="margin-top:20px;text-align:center;">
                            <a-button size="large" style="width:128px;" @click="status='url'">上一步</a-button>
                            <a-button
                                @click="cssnext"
                                type="primary"
                                size="large"
                                style="width:128px;margin-left:20px;"
                            >下一步</a-button>
                        </div>
                    </div>
                    <div v-else-if="status==='phone'" class="insert_two">
                        <div style="display: flex;">
                            <div style="flex: 0 0 30%;">
                                <div class="border">
                                    <a-form
                                        style="margin-top:20px;"
                                        :form="form2"
                                        :label-col="{ span: 8 }"
                                        :wrapper-col="{ span: 16 }"
                                        @submit="handleSubmit2"
                                    >
                                        <a-form-item label="电话号码">
                                            <a-input
                                                size="large"
                                                v-decorator="['phone', { initialValue:phone,rules: [{ required: true,message:'请输入电话号码'}]}]"
                                            />
                                        </a-form-item>
                                        <a-form-item label="电话按钮文字">
                                            <a-input
                                                size="large"
                                                v-decorator="['phonetxt', { initialValue:phonetxt,rules: [{ required: true,message:'请输入电话按钮文字'}]}]"
                                            />
                                        </a-form-item>
                                        <a-form-item
                                            :wrapper-col="{ span: 20, offset: 2 }"
                                            style="margin-top:60px;"
                                        >
                                            <a-button
                                                size="large"
                                                style="width:128px;"
                                                @click="prephone"
                                            >上一步</a-button>
                                            <a-button
                                                size="large"
                                                type="primary"
                                                html-type="submit"
                                                style="width:128px;margin-left:20px;"
                                            >下一步</a-button>
                                        </a-form-item>
                                    </a-form>
                                </div>
                            </div>
                            <div style="flex: 0 0 70%;">
                                <div class="border" style="padding:0;">
                                    <div class="myiframe-box">
                                        <iframe class="myiframe" :src="url" frameborder="0"></iframe>
                                        <div class="qfy-btn-group" :style="styleObject">
                                            <div
                                                v-for="tag in tags"
                                                :key="tag.title"
                                                v-if="tag.checked"
                                            >{{tag.title}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-else-if="status==='wechat'" class="insert_two">
                        <div style="display: flex;">
                            <div style="flex: 0 0 30%;">
                                <div class="border">
                                    <a-form
                                        style="margin-top:20px;"
                                        :form="form3"
                                        :label-col="{ span: 8 }"
                                        :wrapper-col="{ span: 16 }"
                                        @submit="handleSubmit3"
                                    >
                                        <a-form-item label="上传二维码">
                                            <a-upload
                                                v-decorator="['wechat', { initialValue:wechat,rules: [{ required: true,message:'请上传二维码'}]}]"
                                                action="/fileupload/upload"
                                                list-type="picture-card"
                                                :show-upload-list="false"
                                                :beforeUpload="beforeUpload"
                                                @change="handleChange"
                                            >
                                                <img
                                                    v-if="wechat"
                                                    :src="wechat"
                                                    style="width:100px;height:100px;"
                                                />
                                                <div v-else>
                                                    <a-icon :type="imgLoading?'loading':'plus'" />
                                                    <div class="ant-upload-text">上传</div>
                                                </div>
                                            </a-upload>
                                        </a-form-item>
                                        <a-form-item label="微信按钮文字">
                                            <a-input
                                                size="large"
                                                v-decorator="['wechattxt', { initialValue:wechattxt,rules: [{ required: true,message:'请输入微信按钮文字'}]}]"
                                            />
                                        </a-form-item>
                                        <a-form-item
                                            :wrapper-col="{ span: 20, offset: 2 }"
                                            style="margin-top:60px;"
                                        >
                                            <a-button
                                                size="large"
                                                style="width:128px;"
                                                @click="prewechat"
                                            >上一步</a-button>
                                            <a-button
                                                size="large"
                                                type="primary"
                                                html-type="submit"
                                                style="width:128px;margin-left:20px;"
                                            >下一步</a-button>
                                        </a-form-item>
                                    </a-form>
                                </div>
                            </div>
                            <div style="flex: 0 0 70%;">
                                <div class="border" style="padding:0;">
                                    <div class="myiframe-box">
                                        <iframe class="myiframe" :src="url" frameborder="0"></iframe>
                                        <div class="qfy-btn-group" :style="styleObject">
                                            <div
                                                v-for="tag in tags"
                                                :key="tag.title"
                                                v-if="tag.checked"
                                            >{{tag.title}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-else-if="status==='code'">
                        <div class="insert_code">
                            <p>安装代码</p>
                            <p>站点网址：{{url}}</p>
                            <div class="code-copy">{{html}}</div>
                            <!-- <a-button style="width:128px;margin-top:20px;" size="large">暂不安装</a-button>
                            <a-button
                                style="width:128px;margin-left:20px;margin-top:20px;"
                                type="primary"
                                size="large"
                            >完成</a-button>-->
                        </div>
                    </div>
                </div>
            </a-tab-pane>
            <a-tab-pane key="2" tab="APP">Content of Tab Pane 2</a-tab-pane>
            <a-tab-pane key="3" tab="微信公众号">Content of Tab Pane 3</a-tab-pane>
            <a-tab-pane key="4" tab="微信小程序">Content of Tab Pane 3</a-tab-pane>
        </a-tabs>
    </div>
</template>
<script>
export default {
    name: "HelloWorld",
    data() {
        return {
            inserting: false,
            status: "url",
            form: this.$form.createForm(this, {
                name: "coordinated",
            }),
            url: "",
            tags: [
                {
                    code: "1",
                    title: "咨询",
                    checked: true,
                },
                {
                    code: "2",
                    title: "电话",
                    checked: true,
                },
                {
                    code: "3",
                    title: "微信",
                    checked: true,
                },
            ],
            currentIdx: "",
            positionList: [
                {
                    title: "页面左下",
                    value: "1",
                    styleObject: {
                        right: "5px",
                        top: "70%",
                    },
                    styletxt: "right:5px;top:70%;",
                },
                {
                    title: "页面左中",
                    value: "2",
                    styleObject: {
                        right: "5px",
                        top: "50%",
                    },
                    styletxt: "right:5px;top:50%;",
                },
                {
                    title: "页面左上",
                    value: "3",
                    styleObject: {
                        right: "5px",
                        top: "20%",
                    },
                    styletxt: "right:5px;top:20%;",
                },
                {
                    title: "页面右下",
                    value: "4",
                    styleObject: {
                        left: "5px",
                        top: "70%",
                    },
                    styletxt: "left:5px;top:70%;",
                },
                {
                    title: "页面右中",
                    value: "5",
                    styleObject: {
                        left: "5px",
                        top: "50%",
                    },
                    styletxt: "left:5px;top:50%;",
                },
                {
                    title: "页面右上",
                    value: "6",
                    styleObject: {
                        left: "5px",
                        top: "20%",
                    },
                    styletxt: "left:5px;top:20%;",
                },
            ],
            position: "1",
            styleObject: { right: "5px", top: "70%" },
            form2: this.$form.createForm(this, {
                name: "coordinated2",
            }),
            phone: "",
            phonetxt: "电话",
            form3: this.$form.createForm(this, {
                name: "coordinated3",
            }),
            // wechat: "http://www.qftx.com/img/home-swiper-img3.16b79efb.png",
            wechat: "",
            imgLoading: false,
            wechattxt: "微信",
            nowurl: "",
            html: "",
        };
    },
    computed: {
        phoneDisabled() {
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].title == "电话") {
                    return !this.tags[i].checked;
                }
            }
        },
        wechatDisabled() {
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].title == "微信") {
                    return !this.tags[i].checked;
                }
            }
        },
    },
    methods: {
        beforeUpload(file) {
            if (this.imgLoading) {
                return false;
            }
            this.imgLoading = true;
            let arr = [
                "image/jpeg",
                "image/jpg",
                "image/gif",
                "image/png",
                "image/bmp",
            ];
            if (arr.indexOf(file.type) === -1) {
                message.error("照片支持.jpg .jpeg .bmp .gif .png格式");
                return false;
            }
            if (file.size / 1024 / 1024 > 5) {
                message.error("图片大小不超过5M");
                return false;
            }
            return true;
        },
        handleChange({ file, fileList }) {
            if (file.status === "uploading") {
            } else if (file.status === "error") {
                this.imgLoading = false;
            } else if (file.status === "done") {
                this.imgLoading = false;
                let timer = setTimeout(() => {
                    this.form3.setFieldsValue({
                        wechat: file.response.url,
                    });
                    this.wechat = file.response.url;
                    timer && clearTimeout(timer);
                }, 100);
            } else {
                let timer = setTimeout(() => {
                    this.form3.setFieldsValue({
                        wechat: "",
                    });
                    this.wechat = "";
                    timer && clearTimeout(timer);
                }, 100);
            }
        },
        callback(key) {
            console.log(key);
        },
        validateToUrl(rule, value, callback) {
            let reg = /^(?:(http|https|ftp):\/\/)?((?:[\w-]+\.)+[a-z0-9]+)((?:\/[^/?#]*)+)?(\?[^#]+)?(#.+)?$/i;
            if (!reg.test(value)) {
                callback("请输入正确的网址");
            } else {
                callback();
            }
        },
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFields((err, values) => {
                if (!err) {
                    this.status = "css";
                    this.url = values.url;
                }
            });
        },
        onChange(e, f) {
            this.tags[this.currentIdx].checked = e.target.checked;
        },
        up(idx) {
            let tags = this.tags;
            let arr = tags.splice(idx, 1);
            tags.splice(idx - 1, 0, arr[0]);
            this.tags = tags;
        },
        down(idx) {
            let tags = this.tags;
            let arr = tags.splice(idx, 1);
            tags.splice(idx + 1, 0, arr[0]);
            this.tags = tags;
        },
        handleSelectChange(value) {
            if (value) {
                let num = parseInt(value);
                this.styleObject = this.positionList[num - 1].styleObject;
            }
        },
        cssnext() {
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].checked && this.tags[i].code === "2") {
                    this.status = "phone";
                    return;
                } else if (this.tags[i].checked && this.tags[i].code === "3") {
                    this.status = "wechat";
                    return;
                }
            }
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].checked && this.tags[i].code === "1") {
                    this.status = "code";
                    this.getNowUrl();
                    return;
                }
            }
        },
        prephone() {
            let num1 = -1;
            let num2 = -1;
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].checked && this.tags[i].code === "2") {
                    num1 = i;
                }
                if (this.tags[i].checked && this.tags[i].code === "3") {
                    num2 = i;
                }
            }
            if ((num2 !== -1 && num1 < num2) || num2 === -1) {
                this.status = "css";
            } else {
                this.status = "wechat";
            }
        },
        handleSubmit2(e) {
            e.preventDefault();
            this.form2.validateFields((err, values) => {
                if (!err) {
                    this.phone = values.phone;
                    this.phonetxt = values.phonetxt;
                    let num1 = -1;
                    let num2 = -1;
                    for (let i = 0; i < this.tags.length; i++) {
                        if (this.tags[i].checked && this.tags[i].code === "2") {
                            num1 = i;
                        }
                        if (this.tags[i].checked && this.tags[i].code === "3") {
                            num2 = i;
                        }
                    }
                    if (num2 === -1 || (num2 !== -1 && num1 > num2)) {
                        this.status = "code";
                        this.getNowUrl();
                    } else {
                        this.status = "wechat";
                    }
                }
            });
        },
        prewechat() {
            let num1 = -1;
            let num2 = -1;
            for (let i = 0; i < this.tags.length; i++) {
                if (this.tags[i].checked && this.tags[i].code === "3") {
                    num1 = i;
                }
                if (this.tags[i].checked && this.tags[i].code === "2") {
                    num2 = i;
                }
            }
            if ((num2 !== -1 && num1 < num2) || num2 === -1) {
                this.status = "css";
            } else {
                this.status = "phone";
            }
        },
        handleSubmit3(e) {
            e.preventDefault();
            this.form3.validateFields((err, values) => {
                if (!err) {
                    this.wechat = values.wechat;
                    this.wechattxt = values.wechattxt;
                    let num1 = -1;
                    let num2 = -1;
                    for (let i = 0; i < this.tags.length; i++) {
                        if (this.tags[i].checked && this.tags[i].code === "3") {
                            num1 = i;
                        }
                        if (this.tags[i].checked && this.tags[i].code === "2") {
                            num2 = i;
                        }
                    }
                    if (num2 === -1 || (num2 !== -1 && num1 > num2)) {
                        this.status = "code";
                        this.getNowUrl();
                    } else {
                        this.status = "phone";
                    }
                }
            });
        },
        getNowUrl() {
            let nowurl = `${window.location.origin}/static/qfy/index.html?position=${this.position}`;
            let tag = [];
            for (let i = 0; i < this.tags.length; i++) {
                tag.push(this.tags[i].code);
                if (this.tags[i].checked && this.tags[i].code === "2") {
                    nowurl += `&tel=${this.phone}`;
                    nowurl += `&teltxt=${this.phonetxt}`;
                }
                if (this.tags[i].checked && this.tags[i].code === "3") {
                    nowurl += `&wechat=${encodeURIComponent(this.wechat)}`;
                    nowurl += `&wechattxt=${this.wechattxt}`;
                }
                if (this.tags[i].checked && this.tags[i].code === "1") {
                    nowurl += `&zx=1`;
                }
            }
            nowurl += `&tag=${tag.join(",")}`;
            this.html = `<iframe class="qfy-iframe" frameborder="0" src="${nowurl}" style="height:156px;width:204px;position:fixed;${
                this.positionList[parseInt(this.position) - 1].styletxt
            }"></iframe>`;
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.insert_none {
    margin-top: 14em;
    text-align: center;
    p {
        font-size: 24px;
        line-height: 32px;
        color: #0d1a26;
        margin-bottom: 1em;
        font-weight: 500;
    }
}
.insert_code {
    margin-top: 4em;
    text-align: center;
    p {
        font-size: 24px;
        line-height: 32px;
        color: #0d1a26;
        margin-bottom: 1em;
        font-weight: 500;
    }
    .code-copy {
        margin: 0 auto 60px;
        text-align: left;
        width: 600px;
        padding: 8px;
        border: 1px solid #c4c7ce;
    }
}

.btn-group-noe {
    margin-top: 2em;
    display: flex;
    justify-content: space-around;
    button:active,
    button:hover,
    button:focus {
        color: rgba(0, 0, 0, 0.65);
        border-color: #d9d9d9;
    }
}

.insert_start {
    margin-top: 8em;
}

.activeBtn {
    color: #1890ff;
    border-color: #1890ff;
}

.insert_two {
    margin: 1em;
}

.border {
    border: 1px solid #ebedf0;
    padding: 10px 20px;
    margin: 0 5px;
    box-sizing: border-box;
}

.checkbox-item {
    margin: 20px 0;
    display: flex;
    p {
        flex: 1;
        text-align: right;
        i {
            margin-left: 5px;
            cursor: pointer;
        }
    }
}

.radio-item-list {
    margin: 20px 0;
    text-align: center;
}

.myiframe-box {
    height: 500px;
    overflow: hidden;
    position: relative;
    .qfy-btn-group {
        position: absolute;
        width: 50px;
        height: 150px;
        margin-top: -75px;
        & > div {
            text-align: center;
            height: 50px;
            line-height: 50px;
            border: 1px solid #c4c7ce;
            cursor: pointer;
        }
    }
}
.myiframe {
    width: 200%;
    height: 1000px;
    transform: translate(-25%, -25%) scale(0.5);
}
</style>



